// -------------- THEME SWITCHER -------------- //
@mixin dark-appearance {
  filter: invert(1);
  img {
    filter: invert(1);

    &.ioda { filter: invert(0); }
  }
}

body[a="dark"] { @include dark-appearance; }


@media (prefers-color-scheme: dark) {
  body[a="auto"] { @include dark-appearance; }
}
// -------------------------------------------- //

// bg color is also needed in html in order to
// block body's background propagation
// see: https://stackoverflow.com/a/61265706
html, body { background: white; }

html { height: 100%; }

body {
  color: black;
  font-family: monospace;
  font-size: 16px;
  line-height: 1.4;
  margin: 0;
  min-height: 100%;
  overflow-wrap: break-word;
}

.post-meta { text-align: right; }

h2, h3, h4, h5, h6 { margin-top: 3rem; }

hr { margin: 2rem 0; }

p { margin: 1rem 0; }

li { margin: 0.4rem 0; }

*:target { background: yellow; }

.w {
  max-width: 640px;
  margin: 0 auto;
  padding: 4rem 2rem;
}

hr {
  text-align: center;
  border: 0;

  &:before { content: '/////' }
  &:after { content: attr(data-content) '/////' }
}

pre {
  padding: 1em;
}

table {
  width: 100%;
  // make the table smaller and break the words apart once the screen gets smaller
  table-layout: fixed;
  word-wrap: break-word;
}

table, th, td {
  border: thin solid black;
  border-collapse: collapse;
  padding: 0.4rem;
}

code {
  color: white;
  background: black;

  // Added to wrap the texts on codeblocks, it's overflowing without this
  white-space: pre-wrap;   /* allow wrapping */
  word-wrap: break-word;   /* old browsers */
  overflow-wrap: break-word; /* modern browsers */
}

code.has-jax {
  -webkit-font-smoothing: antialiased;
  background: inherit !important;
  border: none !important;
  font-size: 100%;
}

blockquote {
  font-style: italic;
  border: thin solid black;
  padding: 1rem;

  p { margin: 0; }
}

img {
  max-width: 100%;
  display: block;
  margin: 0 auto;
}

a {
  color:blue
}

table {
  // makes the table fonts smaller on smaller screens

  @media (max-width: 430px) {
    font-size: 14px;

    th, td {
      padding: 4px; // optional, makes it less cramped
    }
  }
}

// Used in My ACL Reconstruction Log Post
table.my-acl-reconstruction-log-table {
  width: 100% !important;
  table-layout: fixed !important; // force equal-width cols
  border-collapse: collapse;
}

table.my-acl-reconstruction-log-table th,
table.my-acl-reconstruction-log-table td {
  white-space: normal !important; // allow wrapping
  word-break: break-word !important; // break long words if needed
  padding: 0.5em;
}

// Column widths
table.my-acl-reconstruction-log-table td:nth-child(1),
table.my-acl-reconstruction-log-table th:nth-child(1) { width: 20%; text-align: center;}

table.my-acl-reconstruction-log-table td:nth-child(2),
table.my-acl-reconstruction-log-table th:nth-child(2) { width: 55%; }

table.my-acl-reconstruction-log-table td:nth-child(3),
table.my-acl-reconstruction-log-table th:nth-child(3) { width: 13%; text-align: center;}

table.my-acl-reconstruction-log-table td:nth-child(4),
table.my-acl-reconstruction-log-table th:nth-child(4) { width: 13%; text-align: center;}
// Used in My ACL Reconstruction Log Post